import api from '../../axios';


// 
export function getCityInfo(name) {
  return api.get('/area/info', {
    params: {
      name
    }
  })
}

// 
export function getCityList(level = 1) {
  return api.get('/area/city', {
    params: {
      level
    }
  })
}

// 
export function getHotCity() {
  return api.get('/area/hot')
}



