/**
 * =
 */
import React, { Component } from 'react';
import { getCityList, getHotCity } from '../../utils/api/City';
import { getCurCity, setLocal, CURR_CITY } from '../../utils';

import { List, AutoSizer } from 'react-virtualized';

import './index.scss'
import { NavBar, Icon, Toast } from 'antd-mobile';

class CityList extends Component {

  // 
  state = {
    // 
    cityList: {},
    // 
    cityIndex: [],
    // 
    activeIndex: 0,
  }


  componentDidMount() {
    this.getCityList()
  }


  // title
  formatLetter = (letter, isRight) => {
    switch (letter) {
      case '#':
        return isRight ? '' : '';
      case 'hot':
        return isRight ? '' : '';
      default:
        // 
        return letter.toUpperCase()
    }
  }

  // 
  changeCity = (item) => {
    // 
    const hasData = ['', '', '', ''];
    if (hasData.includes(item.label)) {
      // 
      setLocal(CURR_CITY, JSON.stringify(item));
      // 
      this.props.history.push('/')
    } else {
      Toast.info('')
    }

  }


  rowRenderer = ({
    key, // Unique key within array of rows
    index, // Index of row within collection
    isScrolling, // The List is currently being scrolled
    isVisible, // This row is visible within the List (eg it is not an overscanned row)
    style, // Style object to be applied to row (to position it)
  }) => {
    // 
    const { cityList, cityIndex } = this.state;
    // 
    let letter = cityIndex[index];
    // console.log(index,letter)
    // 
    let item = cityList[letter];
    // console.log(letter, item)
    return (
      <div key={key} style={style} className="city-item">
        <div className="title">{this.formatLetter(letter)}</div>
        {
          item.map((item) => <div onClick={() => this.changeCity(item)} key={item.value} className="name">{item.label}</div>)
        }
      </div>
    );
  }


  // 
  getCityList = async () => {
    const { status, data } = await getCityList();
    if (status === 200) {
      // 
      let { cityList, cityIndex } = this.formatCities(data);
      // 
      const { status: st, data: hot } = await getHotCity();
      if (st === 200) {
        cityList['hot'] = hot;
        cityIndex.unshift('hot')
      }
      // 
      const res = await getCurCity();
      cityList['#'] = [res];
      cityIndex.unshift('#')
      // 
      this.setState({
        cityList,
        cityIndex
      })

      // console.log(cityList, cityIndex)
    }
  }

  // 
  formatCities = (data) => {
    // 
    let cityList = {}, cityIndex;
    // 
    data.forEach((item) => {
      // 
      let first = item.short.slice(0, 1);
      // 
      // 
      if (!cityList[first]) {
        // 
        cityList[first] = [item]
      } else {
        // 
        cityList[first].push(item)
      }
    })
    // console.log(':', cityList);
    // 
    cityIndex = Object.keys(cityList).sort();
    // console.log(':', cityIndex);
    // // 
    // cityIndex.map((item) => {
    //   console.log(item, cityList[item])
    // })

    return {
      cityList,
      cityIndex
    }

  }

  // 
  excueHeight = ({ index }) => {
    const { cityIndex, cityList } = this.state;
    // title50+ *36
    // 
    let curKey = cityIndex[index];
    // console.log(curKey, cityList[curKey])
    return 36 + cityList[curKey].length * 50
  }

  // 
  renderCityIndex = () => {
    const { cityIndex, activeIndex } = this.state;
    return cityIndex.map((item, index) => {
      return (
        <li
          key={item}
          className="city-index-item"
          onClick={() => {
            // 
            // console.log(this.listRef.scrollToRow)
            this.listRef.scrollToRow(index);
            // this.setState({
            //   activeIndex: index
            // })
          }}
        >
          <span className={activeIndex === index ? 'index-active' : ''}>
            {this.formatLetter(item, true)}
          </span>
        </li>
      )
    })
  }

  // ()
  onRowsRendered = ({ startIndex }) => {
    if (this.state.activeIndex !== startIndex) {
      console.log(startIndex);
      // startIndex 
      this.setState({
        activeIndex: startIndex
      })
    }
  }

  render() {
    return (
      <div className="cityList">
        {/*  */}
        <NavBar
          mode="dark"
          icon={<Icon type="left" />}
          onLeftClick={() => this.props.history.goBack()}
        ></NavBar>
        {/*  */}
        <AutoSizer>
          {({ height, width }) => (
            <List
              ref={(ele) => this.listRef = ele}
              scrollToAlignment='start'
              onRowsRendered={this.onRowsRendered}
              height={height}
              rowCount={this.state.cityIndex.length}
              rowHeight={this.excueHeight}
              rowRenderer={this.rowRenderer}
              width={width}
            />
          )}
        </AutoSizer>
        {/*  */}
        <ul className="city-index">
          {this.renderCityIndex()}
        </ul>
      </div>
    );
  }
}

export default CityList;